@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap");

body {
  font-family: "Lato", sans-serif;
  background-color: #1e3231;
}

.flex {
  display: flex;
}

.container-1 {
  padding-right: 108px;
  padding-left: 108px;
  background-image: url(resources/Landing\ Area\ Image.png);
  background-size: cover;
  background-repeat: no-repeat;

  position: relative;
  z-index: -5;
}

.nav__wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 67px;
}

.nav__logo__title {
  font-weight: 700;
  font-size: 40px;
  line-height: 48px;
  color: #d0a5c0;
}

.nav__logo__subtitle {
  color: white;
  display: flex;
  align-items: flex-end;
}

.nav__item {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  display: flex;
  align-items: flex-end;
  margin-right: 48px;
  color: white;

  transition: all 0.3s linear;
  cursor: pointer;
}

.nav__item:hover {
  color: #e0cbcbd7;
}

.main-text {
  margin-top: 128px;
  color: white;
}

.title {
  font-weight: 700;
  font-size: 64px;
  line-height: 64px;
}

.title span {
  font-weight: 900;
}

.subtitle {
  margin-top: 47px;
  font-size: 20px;
  line-height: 24px;
  margin-bottom: 30px;
}

.btn {
  color: white;
  font-size: 20px;
  line-height: 24px;
  background: #d0a5c0;
  padding: 10px;
  border-radius: 37px;
  cursor: pointer;

  transition: all 0.3s ease;
}

.btn:hover {
  background: #917285;
}

.search-area {
  background-color: white;
  border-radius: 61px;
  margin-top: 100px;
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.vector {
  position: absolute;
  width: 100%;
  z-index: -1;
}

.search__title {
  font-weight: 700;
  font-size: 36px;
  line-height: 34px;
}

.search__input {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #c4c4c466;
  color: rgba(0, 0, 0, 0.5);
  border-radius: 34px;
  margin-top: 33px;
  margin-bottom: 33px;
  width: 700px;
  padding: 10px;
  padding-left: 25px;
  padding-right: 25px;
  cursor: pointer;
}

.search__btn {
  background: #d0a5c0;
  padding: 10px 50px;
  border-radius: 37px;
  color: white;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;

  transition: all 0.3s linear;
}

.search__btn:hover {
  background: #917285;
}

.container-2 {
  background-image: url(resources/Background-about-us.png);
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  z-index: -6;
  padding-top: 200px;
  padding-bottom: 200px;
}

.about {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.second-title {
  text-align: center;
  margin-bottom: 115px;
  color: white;
  font-weight: 900;
}

.about__item__name {
  font-weight: 700;
  color: white;
  font-size: 36px;
  line-height: 96%;
  margin-bottom: 28px;
}

.about__item__desc {
  color: white;
  font-size: 20px;
  line-height: 24px;
  border-right: 3px solid white;
  padding-right: 50px;
}

.about__item__descc {
  color: white;
  font-size: 20px;
  line-height: 24px;
}

.container-3 {
    background-color: #485665;
    padding-bottom: 25px;
}

.third-title {
    color: white;
    padding-left: 110px;
    padding-top: 65px;
    padding-bottom: 50px;
}

.destinations-item {
    background: #1e3231;
    border-radius: 41px;
    margin: 66px;
    padding: 48px;
    background-image: url(resources/unsplash_vWxE6Ejr9j0.png);
    background-repeat: no-repeat;
}

.destinations-item .name {
    font-weight: 900;
    font-size: 40px;
    line-height: 38px;
    color: white;
}

.destinations-item .suptitle {
    font-weight: 700;
    font-size: 32px;
    line-height: 30px;
    color: white;
    margin-top: 17px;
}

.destinations-item .desc {
    color: white;
    font-size: 20px;
    line-height: 24px;
    margin-top: 39px;
    padding-bottom: 75px;
    border-bottom: 2px solid white;
}

.destinations-item .buttons {
    margin-top: 66px;
}

.learnMore {
    margin-right: 20px;
    color: white;
    font-weight: 700;
    font-size: 20px;
}

.bookNow {
    font-weight: 700;
    font-size: 24px;
    color: white;
    background-color: #d0a5c0;
    padding: 15px 45px;
    border-radius: 26px;
}

.company-name {
    font-weight: 700;
    font-size: 40px;
    line-height: 48px;
    color: #d0a5c0;
    margin-top: 100px;
    text-align: center;
}

.company-anthem {
    color: white;
    text-align: center;
    line-height: 20px;
    margin-bottom: 100px;
}

